* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.navbar{
  color: #fff;
  background-color: deepskyblue;
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 35px;
}
.footer{
  color:#fff;
  background-color: deepskyblue;
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
}
.content{
  min-height: 80vh;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: left;
}
.tabs {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 20px 0;
}
.tab-leagues {
  width: 300px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #00bfff;
  color: #000;
  text-align: center;
  border-right: 5px solid #cccccc;
}
.tab-standings {
  width: 300px;
  background-color: #00bfff;
  color: #000;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
}
.leagues{
  width: 100%;
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(4, 1fr);
  text-align: center;
}
.league-div {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 50px 0;
}
.league-div > img {
  width: 250px;
}
.league-div:hover {
  transform: scale(1.1);
}
select {
  padding: 10px;
  font-size: 19px;
  color: #00bfff;
  background-color:#fff;
}
.ranking-div {
  align-items: center;
  justify-content: center;
  flex-direction: column;
  margin: 20px 0;
  width: 100%;
  display: grid;
  justify-items: center;
  grid-template-columns: repeat(4, 1fr);
  text-align: center;
}
.ranking-div-inner {
  margin: 20px 0;
}
.logo-small {
  width: 70px;
}
